import img1 from '../photo1/photo1_1.jpg';
import img2 from '../photo1/photo1_2.jpg';
import img3 from '../photo1/photo1_3.jpg';
import img4 from '../photo1/photo1_4.jpg';

function Home() {
  const images = [
    {
      id: 0,
      src: img1,
      alt: img1
    },
    {
      id: 1,
      src: img2,
      alt: img2
    },
    {
      id: 2,
      src: img3,
      alt: img3
    },
    {
      id: 3,
      src: img4,
      alt: img4
    },
  ];

  return (
    <div className="imageContainer">
    {images.map(image => <img src={image.src} 
    alt ={image.alt} className="homeImage" key={image.id} />)}
    </div>
  );
}

export default Home;